  <v-app id="app">
    <v-app-bar app dense dark>
      <v-toolbar-title>Page title</v-toolbar-title>
    </v-app-bar>
    <v-main>
      <!-- <v-container> -->
        <router-view />
        <v-btn to="/tabRecord" class="center-btn" elevation="0" href="#" color="red" fixed middle fab>
          <fa-icon icon="dot-circle"></fa-icon>
        </v-btn>
      <!-- </v-container> -->
    </v-main>
    <Tabs></Tabs>
  </v-app>
</template>

<script>
import Tabs from './components/Tabs'
export default {
  name: 'App',
  components: { Tabs }
}
</script>

<style>
.center-btn {
  z-index: 100 !important;
  bottom: 30px;
  transform: translateX(-50%);
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



